* {
  -webkit-touch-callout:none;
  -webkit-text-size-adjust:none;
  -webkit-tap-highlight-color:rgba(0,0,0,0);
  -webkit-user-select:none;
}

.game {
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: #444;
  font-family: 'Helvetica', sans-serif, 'Arial';
}

.cell {
  position: relative;
  float: left;
  cursor: pointer;
}

.inner {
  position: absolute;
  border-radius: 2%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  color: #fff;
  margin: .2vw .2vw;
  background-color: #DCDCDC;
}

.rules {
  font-size: .4em;
}

.button {
  background-color: blue;
  color: #fff;
  border-radius: 3%;
  text-decoration: none;
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  cursor: pointer;
  outline: 0 !important;
}

.button:hover {
  background-color: #1E90FF;
}

.show-message {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.hide-message {
  display: none;
}

.TAP {
  background-color: green;
}

.WAIT {
  background-color: red;
}



